
namespace Serenity {

    export class LookupEditorHideInactive<TOptions extends LookupEditorOptions, TItem> extends Serenity.LookupEditorBase<TOptions, TItem> {

        protected getItemDisabled(item: TItem, lookup: Q.Lookup<TItem>): boolean {
            return super.getItemDisabled(item, lookup) ||
                this.getItemInactive(item, lookup);
        }

        protected hideInactive() {
            return true;
        }

        protected getItemInactive(item: TItem, lookup: Q.Lookup<TItem>): boolean {
            return false;
        }

        private settingValues: string[] = null;

        protected filterItems(items: TItem[]) {
            items = super.filterItems(items);
            if (!this.hideInactive())
                return items;

            var lookup = this.getLookup();
            if (lookup == null)
                return items;

            var values = this.settingValues == null ? this.values : this.settingValues;

            return items.filter(item => 
                !this.getItemInactive(item, lookup) ||
                this.values.indexOf(item[lookup.idField]) >= 0);
        }

        public set_values(values: string[]) {
            this.settingValues = values || [];
            try {
                this.hideInactive() && this.updateItems();
                super.set_values(values);
            }
            finally {
                this.settingValues = null;
            }
        }

        public set_value(value: string) {
            this.settingValues = value == null || value === '' ? [] : [value];
            try {
                this.hideInactive() && this.updateItems();
                super.set_value(value);
            }
            finally {
                this.settingValues = null;
            }
        }
    }
}

namespace Kun.Northwind {

    @Serenity.Decorators.registerEditor()
    export class CustomerEditor extends Serenity.LookupEditorHideInactive<Serenity.LookupEditorOptions, CustomerRow> {

        constructor(hidden: JQuery, options: Serenity.LookupEditorOptions) {
            super(hidden, options);
        }

        protected getLookupKey() {
            return 'Northwind.Customer';
        }

        protected getItemText(item, lookup) {
            return super.getItemText(item, lookup) + ' [' + item.CustomerID + ']';
        }

        protected getItemInactive(item: CustomerRow, lookup: Q.Lookup<CustomerRow>): boolean {
            return item.Country != "USA";
        }
    }
}